  height: 16px;
  background-image: url("../img/content/marker-small.png");
  transition: 0.4s;
}

.icon-markerSmall:hover {
  background-image: url("../img/content/marker-small-hover.png");
}

.chooseCity__btn:hover .icon-markerSmall {
  background-image: url("../img/content/marker-small-hover.png");
}

